.primary-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  column-gap: 150px;
  row-gap: 10px;
  // background-color: rgba(128, 128, 128, 0.364);
  @include xxlMax {
    column-gap: 110px;
  }
  @include xlMax {
    column-gap: 70px;
  }
  @include lgMax {
    column-gap: 50px;
  }
  @include mdMax {
    column-gap: 40px;
    flex-direction: column;
  }
  &.reverse {
    flex-direction: row-reverse;
    @include mdMax {
      flex-direction: column;
    }
  }
  h2 {
    @include title("secondary", var(--primary-dark));
    margin-bottom: 20px;
    @include smMax {
      margin-bottom: 10px;
    }
  }
  img {
    max-height: 600px;
    max-width: 650px;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  .swiper {
    width: 100%;
    height: 100%;
  }
  .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .swiper-pagination {
    display: flex;
    justify-content: center;
    gap: 5px;
    position: static;
    margin-top: 20px;
    .swiper-pagination-bullet {
      margin: 0 !important;
      background-color: var(--gray-700);
      opacity: 0.5;
      width: 10px;
      height: 10px;
      @include smMax {
        width: 8px;
        height: 8px;
      }
    }
    .swiper-pagination-bullet-active {
      opacity: 1;
    }
  }
}
.primary-section__image,
.primary-section__content {
  // background-color: gray;
  position: relative;
  width: 50%;
  @include mdMax {
    width: 100%;
  }
}
.primary-section__form {
  padding: 24.5% 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
  gap: 20px;
  h3 {
    @include title("secondary", var(--primary-dark));
    margin: 0;
  }
  @include mdMax {
    padding: 0;
    background: none;
  }
  @include smMax {
    gap: 15px;
  }
}
.primary-section__content {
  .button-group {
    display: flex;
    flex-wrap: wrap;
    gap: 60px;
    row-gap: 20px;
    margin-top: 40px;
    @include xlMax {
      gap: 20px;
    }
    @include smMax {
      flex-direction: column;
      margin-top: 20px;
    }
  }
  &__cover {
    position: relative;
    padding: 40px 40px 40px 140px;
    border-radius: 20px;
    background: var(--primary-light);
    box-shadow: 0px 4px 20px 0px rgba(73, 169, 66, 0.2);
    @include xlMax {
      padding: 20px 20px 20px 80px;
    }
    &::before {
      content: "";
      position: absolute;
      top: -80px;
      left: -70px;
      display: block;
      width: 215px;
      height: 182px;
      rotate: -20.5deg;
      background-size: 100% 100%;
      background-position: center;
      background-image: url(data:image/svg+xml;base64,);
      @include xlMax {
        top: -10px;
        left: -10px;
        width: 88px;
        height: 76px;
        rotate: -27.5deg;
      }
    }
  }
  &__wrap {
    p {
      @include text("tertiary");
      margin-bottom: 20px;
      line-height: 150%;
      @include smMax {
        margin-bottom: 10px;
      }
    }
    ul {
      @include list-style(var(--primary-dark));
      margin: 20px 0;
    }
    a {
      @include link-style;
    }
  }
}
